import React from 'react'
import styled from 'styled-components'

const Window1 = styled.div`
  height: 100vh;
  width: 100%;
  background-image: url(/static/the-creative-exchange-373981-unsplash.jpg);
  background-size: cover;
  background-attachment: fixed;
  h1 {
    position: relative;
    margin: 0 auto;
    width: 40%;
    text-align: center;
    top: 40vh;
    font-family: "fjalla one";
    text-transform: uppercase;
    text-shadow: 2px 0px 5px black;
    font-size: 6vw;
    color: rgb(255, 255, 255);
  }
`;

const Heading = styled.div`
  height: 25vh;
  width: 100%;
  h2 {
    border: 1px dotted white;
    font-family: "fjalla one";
    font-size: 2.3vw;
    color: white;
    width: 30%;
    margin: 0 auto;
    text-align: center;
    letter-spacing: 2px;
    margin-top: 14vh;
    padding: 6px;
    text-transform: uppercase;
  }
`;

const Window2 = styled.div`
  height: 50vh;
  width: 100%;
  background-image: url(/static/tyler-nix-489782-unsplash.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
`;

const Para = styled.div`
  color: white;
  font-family: "fjalla one";
  font-size: 1.5vw;
  text-align: center;
  margin-top: 8vh;
  letter-spacing: 1.2px;
`;

const Block1 = styled.div`
  position: relative;
  height: 80vh;
  width: 50%;
  margin: 0 auto;
  margin-top: 2.5vh;
  margin-bottom: 15vh;
  background-image: url(/static/nathan-dumlao-298337-unsplash.jpg);
  background-size: cover;
  background-position: center;
`;

const Window3 = styled.div`
  height: 60vh;
  width: 100%;
  background-image: url(/static/crew-89808-unsplash.jpg);
  background-size: cover;
  background-position: bottom;
  background-attachment: fixed;
`;

const Block2 = styled.div`
  position: relative;
  height: 70vh;
  width: 60%;
  margin: 0 auto;
  margin-top: 2.5vh;
  margin-bottom: 6vh;
  background-image: url(/static/rawpixel-com-256640-unsplash.jpg);
  background-size: cover;
  background-position: center;
`;

const Footer = styled.div`
  width: 100%;
  height: 55vh;
  bottom: 4vh;
`;

const ContactEnq = styled.div`
  position: relative;
  width: 20%;
  height: 100%;
  left: 25vw;
  float: left;
`;

const EnqCont = styled.div`
  h3 {
    color: white;
    font-family: "fjalla one";
    letter-spacing: 1px;
    font-size: 1.5vw;
    margin: 0;
    margin-top: 4vh;
    margin-bottom: 2vh;
    padding: 0;
  }
`;

const ContInfo = styled.div`
  p {
    position: relative;
    color: white;
    font-family: "fjalla one";
    letter-spacing: 1px;
    bottom: 3px;
    font-size: 1.5vw;
    margin: 0;
    padding: 0;
  }
`;

const OpeningHours = styled.div`
  position: relative;
  width: 20%;
  height: 100%;
  right: 25vw;
  float: right;
  h3 {
    color: white;
    font-family: "fjalla one";
    letter-spacing: 1px;
    font-size: 1.5vw;
    margin: 0;
    margin-top: 4vh;
    padding: 0;
    margin-bottom: 10px;
  }
  ul {
    margin: 0;
    padding: 0;
    margin-top: 30px;
  }
    li {
      color: white;
      list-style-type: none;
      font-family: "fjalla one";
      letter-spacing: 1px;
      font-size: 1.5vw;
      margin-bottom: 2vh;
    }
`;

const IndexPage = () => (
  <div>
    <Window1>
      <h1>Welcome.</h1>
    </Window1>

    <Heading>
      <h2>Freshly ground beans</h2>
    </Heading>

    <Window2></Window2>

    <Para>
      Enjoy the finest blends of ethically sourced Columbian coffee
    </Para>

    <Block1></Block1>

    <Window3></Window3>

    <Para>
      Come dine with us and enjoy a fine culinary experience
    </Para>

    <Block2></Block2>

    <Footer>
      <ContactEnq>

        <EnqCont>
          <h3>Catering Enquiries</h3>
        </EnqCont>

        <ContInfo>
          <p>Mike: 0433 785 554</p>
        </ContInfo>

        <EnqCont>
          <h3>Function Enquiries</h3>
        </EnqCont>

        <ContInfo>
          <p>Minet: 0433 785 555</p>
        </ContInfo>

        <EnqCont>
          <h3>Marketing Enquiries</h3>
        </EnqCont>

        <ContInfo>
          <p>Daryl: 0433 785 556</p>
        </ContInfo>

      </ContactEnq>
      <OpeningHours> 
        <h3>Opening Hours</h3>
          <ul>
            <li>Mon - Fri: 7am - 5pm</li>
            <li>Sat - Sun: 8am - 4pm</li>
            <li>Public Holidays: 8am - 4pm</li>
            <li>(Kitchen closes at 3pm daily)</li>
          </ul>
      </OpeningHours>
    </Footer>
  </div>  
)

export default IndexPage



